<template>
  <div class="home">
      <h2>Ойнакчыларнын данзызы</h2>
      <button @click="view()">click</button>
  </div>
</template>

<script>
// @ is an alias to /src
import bridge from '@vkontakte/vk-bridge';
export default {
  name: 'HomeView',
  methods: {
    view(){
      bridge.send('VKWebAppGetUserInfo')
          .then((data) => { 
            if (data.id) {
              // Данные пользователя получены
              console.log(data);
              console.log(data.last_name);
              console.log(data.first_name);
            }    
          })
          .catch((error) => {
            // Ошибка
            console.log(error);
          });
    }
  }
}
</script>
